import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable, of} from 'rxjs';
import {ProductsCategory} from '../product';
import {ProductsCategories} from '../mock/mock-products';

@Injectable({
  providedIn: 'root'
})
export class CategoriesService {

  constructor() {
    this.activeCategory.next(JSON.parse(localStorage.getItem('activeCategory')));
  }

  public activeCategory: BehaviorSubject<number> = new BehaviorSubject(0);

  public changedCategory: number;

  getCategories(): Observable<ProductsCategory[]> {
    return of(ProductsCategories);
  }

  setActiveCategory(id: number) {
    this.changedCategory = id;
    console.log('this.changedCategory', this.changedCategory);
    localStorage.setItem('activeCategory', JSON.stringify(this.changedCategory));
    this.activeCategory.next(JSON.parse(localStorage.getItem('activeCategory')));
  }

}
