import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {Product} from '../product';

@Injectable({
  providedIn: 'root'
})
export class LocalService {

  public selectedProducts = [];

  public isProductAdded$: BehaviorSubject<Product[]> = new BehaviorSubject<Product[]>(null);


  constructor() {
    this.isProductAdded$.next(JSON.parse(localStorage.getItem('selectedProducts')));
  }

  addProduct(product){
    this.selectedProducts.push(product);
    this.updateDataSource();
  }

  deleteProduct(id){
    this.selectedProducts = this.selectedProducts.filter(item => item.id !== id);
    this.updateDataSource();
  }

  updateDataSource() {
    localStorage.setItem('selectedProducts', JSON.stringify(this.selectedProducts));
    this.isProductAdded$.next(JSON.parse(localStorage.getItem('selectedProducts')));
  }
}
